import {
  IClientSideApplicationManifest
} from './../IClientSideApplicationManifest';

import {
  IInternalModuleConfig
} from './../IClientSideComponentLoaderConfig';

const appManifest: IClientSideApplicationManifest = {
  manifestVersion: 2,
  componentType: 'Application',
  id: 'myApplicationId',
  version: '0.0.1',
  loaderConfig: {
    internalModuleBaseUrls: [ 'https://cdn.net/' ],
    entryModuleId: 'main.bundle',
    exportedModuleName: 'foobar',
    scriptResources: {
      'main.bundle': {
        type: 'internal',
        path: 'bundle.script.js'
      } as IInternalModuleConfig
    }
  },

  preloadOptions: {
    shouldPreloadWeb: true,
    shouldPreloadUser: true,
    shouldPreloadList: true,
    shouldPreloadItem: true,
    shouldPreloadQuickLaunch: true,
    preloadListItemQueryOptions: 'query'
  },
  preloadComponents: [
    'framework1',
    'framework2'
  ],

  title: { default: 'foobar' },
  description:  { default: 'foobar baz' }
};

/* tslint:disable:export-name */
export = appManifest;
/* tslint:enable:export-name */
